let setGroup = [
  {
    title: '',
    key: 'top'
  },
  {
    title: '',
    key: 'bottom'
  },
  {
    title: '',
    key: 'left'
  },
  {
    title: '',
    key: 'right'
  }
]

export default {
  // 
  getThemeSetter (setModule, themeList) {
    let themeObj = {
      title: '',
      type: 'themeSelector',
      param: {
        value: setModule['theme'],
        data: themeList
      },
      callback: function (param) {
        setModule['theme'] = param.value
        setModule['heightType'] = param.heightType || 'auto'
        themeObj.param.value = param.value
        this.resetThemeParamsValue(setModule, themeList)
        this.refreshContent()
        this.refreshSetter(true)
      }.bind(this)
    }
    return themeObj
  },
  // 
  resetThemeParamsValue (setModule, themeList) {
    for (let i in themeList) {
      if (themeList[i].value == setModule['theme']) {
        for (let key in themeList[i]) {
          switch (key) {
            case 'value':
            case 'option':
            case 'support':
            case 'setEnabelList':
              break
            default:
              setModule[key] = themeList[i][key]
              break
          }
        }
      }
    }
  },
  // tab
  getRadioTabParam (setModule, param) {
    let Obj = {
      title: param.title,
      type: 'radioTab',
      param: {
        value: setModule[param.tag] || (param.defaultValue || ''),
        data: param.data
      },
      callback: function (acParam) {
        setModule[param.tag] = acParam.value
        this.refreshContent()
        param.callback && param.callback(acParam)
      }.bind(this)
    }
    return Obj
  },
  // textarea
  getTextareaParam (setModule, param) {
    let Obj = {
      title: param.title,
      type: 'textarea',
      param: {
        value: setModule[param.tag] || (param.defaultValue || ''),
        placeholder: param.placeholder || ''
      },
      callback: function (acParam, acType) {
        if (acType == 'focusout') {
          setModule[param.tag] = acParam.value
          this.refreshContent()
          param.callback && param.callback(acParam)
        }
      }.bind(this)
    }
    return Obj
  },
  // selector
  getSelectorParam (setParam, param) {
    let Obj = {
      hideSelectedIcon: true,
      value: setParam[param.tag] || (param.defaultValue || ''),
      option: param.optionObj[setParam[param.tag]] || (param.defaultOption || ''),
      data: param.data,
      callback: function (acParam) {
        setParam[param.tag] = acParam.value
        this.refreshContent()
        param.callback && param.callback(acParam)
      }.bind(this)
    }
    return Obj
  },
  // 
  getSliderParam (setParam, param) {
    let slParam = {
      type: 'sliderGroup',
      title: param.title,
      textParam: {
        param: {
          value: setParam[param.tag] || (param.defaultValue || 100)
        },
        callback: function (acParam, acType) {
          if (acType == 'focusout') {
            setParam[param.tag] = acParam.value
            slParam.sliderParam.param.value = acParam.value
            this.refreshContent()
          }
        }.bind(this)
      },
      sliderParam: {
        param: {
          min: param.min || 0,
          max: param.max || 100,
          value: parseInt(setParam[param.tag]) || (param.defaultValue || 100)
        },
        callback: function (acParam) {
          setParam[param.tag] = acParam.value
          slParam.textParam.param.value = acParam.value
          this.refreshContent()
        }.bind(this)
      }
    }
    return slParam
  },
  // 
  getUploadParam (setParam, param) {
    let Obj = {
      value: setParam[param.tag] || '',
      del: true,
      callback: function (type, acParam) {
        setParam[param.tag] = acParam.value
        param.callback && param.callback(acParam)
        this.refreshContent()
      }.bind(this),
      delCallback: function (type, acParam) {
        setParam[param.tag] = acParam.value
        param.callback && param.callback(acParam)
        this.refreshContent()
      }.bind(this)
    }
    return Obj
  },
  // 
  getImageSizeStyleSetterParam (setParam) {
    const Obj = {
      type: 'imageSizeStyle',
      heightParam: this.getTextareaParam(setParam, {
        tag: 'height'
      }),
      widthParam: this.getTextareaParam(setParam, {
        tag: 'width'
      }),
      borderRadiusParam: this.getTextareaParam(setParam, {
        tag: 'border-radius'
      }),
      alignParam: this.getRadioTabParam(setParam, {
        title: '',
        tag: 'align',
        defaultValue: 'left',
        data: [
          {
            option: '',
            value: 'left'
          },
          {
            option: '',
            value: 'center'
          },
          {
            option: '',
            value: 'right'
          }
        ]
      })
    }
    return Obj
  },
  // 
  getModuleContentStyleParam (setModule, themeList) {
    const Obj = {
      title: '',
      type: 'moduleContentStyle'
    }
    let setEnabelList
    for (let i in themeList) {
      if (themeList[i].value == setModule['theme'] && themeList[i]['setEnabelList']) {
        setEnabelList = themeList[i]['setEnabelList']
      }
    }
    if (setEnabelList) {
      Obj.setList = []
      for (let i in setEnabelList) {
        let setParam = setEnabelList[i]
        switch (setParam.type) {
          case 'textarea':
            Obj.setList.push(this.getTextareaParam(setModule, setParam.param))
            break
          case 'radioTab':
            Obj.setList.push(this.getRadioTabParam(setModule, setParam.param))
            break
          case 'imageSizeStyle':
            Obj.setList.push(Object.assign(this.getImageSizeStyleSetterParam(setModule[setParam.param.tag]), {
              title: setParam.param.title
            }))
            break
        }
      }
    }
    return Obj
  },
  // 
  getActionSetterParam (setModule, refeshCallback) {
    setModule.action = setModule.action || {}
    const acTypeData = [
      {
        option: '',
        value: 0
      },
      {
        option: '',
        value: 1
      },
      {
        option: '',
        value: 2
      },
      {
        option: '',
        value: 3
      },
      {
        option: '',
        value: 4
      },
      {
        option: '',
        value: 5
      }
    ]
    const acTypeObj = {
      0: '',
      1: '',
      2: '',
      3: '',
      4: '',
      5: ''
    }
    let pages = this.contentConfig.pages
    let pops = this.contentConfig.pops
    let pagesData = []
    for (let i in pages) {
      pagesData.push({
        option: pages[i].name,
        value: pages[i].id
      })
    }
    let popsData = []
    for (let i in pops) {
      popsData.push({
        option: pops[i].name,
        value: pops[i].id
      })
    }
    return {
      type: 'actionGroup',
      pagesParam: {
        title: '',
        param: {
          option: setModule.action['pageName'] || '',
          value: setModule.action['pageId'] || '',
          data: pagesData,
          callback: function (param) {
            setModule.action['pageName'] = param.option
            setModule.action['pageId'] = param.value
            refeshCallback && refeshCallback()
          }
        }
      },
      popsParam: {
        title: '',
        param: {
          option: setModule.action['popName'] || '',
          value: setModule.action['popId'] || '',
          data: popsData,
          callback: function (param) {
            setModule.action['popName'] = param.option
            setModule.action['popId'] = param.value
            refeshCallback && refeshCallback()
          }
        }
      },
      acTypeParam: {
        title: '',
        type: 'singleSelector',
        param: {
          option: acTypeObj[setModule.action['acType']] || '',
          value: setModule.action['acType'] || 0,
          data: acTypeData,
          callback: function (param) {
            setModule.action['acType'] = param.value
            refeshCallback && refeshCallback()
          }
        }
      },
      acUrlParam: {
        title: '',
        param: {
          value: setModule.action['acUrl'] || '',
          placeholder: '',
          callback: function (param, acType) {
            if (acType == 'focusout') {
              setModule.action['acUrl'] = param.value
              refeshCallback && refeshCallback()
            }
          }
        }
      },
      acTargetParam: {
        title: '',
        param: {
          value: setModule.action['acTarget'] || '_self',
          data: [
            {
              option: '',
              value: '_self'
            },
            {
              option: '',
              value: '_blank'
            }
          ],
          callback: function (param) {
            setModule.action['acTarget'] = param.value
            refeshCallback && refeshCallback()
          }
        }
      },
      acFunParam: {
        title: '',
        param: {
          value: setModule.action['acFun'] || '',
          placeholder: '',
          callback: function (param, acType) {
            if (acType == 'focusout') {
              setModule.action['acFun'] = param.value
              refeshCallback && refeshCallback()
            }
          }
        }
      }
    }
  },
  // 
  getImageSetter (setModule, tag, title) {
    let Obj = {
      type: 'image',
      title: title || '',
      urlParam: this.getTextareaParam(setModule, {
        tag,
        title: '',
        placeholder: '',
        callback (acParam) {
          Obj.uploadParam.value = acParam.value
        }
      }),
      uploadParam: this.getUploadParam(setModule, {
        tag,
        callback (acParam) {
          Obj.urlParam.param.value = acParam.value
        }
      })
    }
    return Obj
  },
  // 
  getBackgroundSetterParam (setParam) {
    let Obj = {
      title: '',
      type: 'backgroundGroup',
      backgroundColorParam: this.getTextareaParam(setParam, {
        tag: 'background-color',
        title: '',
        placeholder: ''
      }),
      urlParam: this.getTextareaParam(setParam, {
        tag: 'background-image',
        title: '',
        placeholder: '',
        callback (acParam) {
          Obj.uploadParam.value = acParam.value
        }
      }),
      uploadParam: this.getUploadParam(setParam, {
        tag: 'background-image',
        callback (acParam) {
          Obj.urlParam.param.value = acParam.value
        }
      }),
      backgroundPositionParam: this.getRadioTabParam(setParam, {
        title: '',
        tag: 'background-position',
        defaultValue: 'top',
        data: [
          {
            option: '',
            value: 'center'
          },
          {
            option: '',
            value: 'top'
          },
          {
            option: '',
            value: 'bottom'
          }
        ]
      }),
      backgroundRepeatParam: this.getRadioTabParam(setParam, {
        title: '',
        tag: 'background-repeat',
        defaultValue: 'repeat',
        data: [
          {
            option: '',
            value: 'no-repeat'
          },
          {
            option: '',
            value: 'repeat'
          },
          {
            option: '',
            value: 'repeat-x'
          },
          {
            option: '',
            value: 'repeat-y'
          }
        ]
      })
    }
    return Obj
  },
  // 
  getBorderSetterParam (setParam) {
    const borderStyleData = [
      {
        option: '',
        value: ''
      },
      {
        option: '',
        value: 'solid'
      },
      {
        option: '',
        value: 'dashed'
      },
      {
        option: '',
        value: 'dotted'
      }
    ]
    const borderStyleObj = {
      solid: '',
      dashed: '',
      dotted: ''
    }
    const Obj = {
      title: '',
      type: 'borderGroup'
    }
    for (let i in setGroup) {
      Obj[setGroup[i].key] = {
        title: setGroup[i].title,
        sizeParam: this.getTextareaParam(setParam, {
          tag: 'border-' + setGroup[i].key + '-width'
        }),
        colorParam: this.getTextareaParam(setParam, {
          tag: 'border-' + setGroup[i].key + '-color'
        }),
        styleParam: this.getSelectorParam(setParam, {
          tag: 'border-' + setGroup[i].key + '-style',
          data: borderStyleData,
          optionObj: borderStyleObj
        })
      }
    }
    return Obj
  },
  // 
  getMarginPaddingSetterParam (setParam, param) {
    const Obj = {
      title: param.title,
      type: 'marginGroup'
    }
    for (let i in setGroup) {
      Obj[setGroup[i].key] = this.getTextareaParam(setParam, {
        title: setGroup[i].title,
        tag: param.type + '-' + setGroup[i].key
      })
    }
    return Obj
  },
  // 
  getHeightSetterParam (setParam) {
    const Obj = {
      title: '',
      type: 'heightGroup',
      typeParam: this.getRadioTabParam(setParam, {
        tag: 'heightType',
        defaultValue: 'auto',
        data: [
          {
            option: '',
            value: 'auto'
          },
          {
            option: '',
            value: 'set'
          },
          {
            option: '',
            value: 'screen'
          }
        ]
      }),
      overflowParam: this.getRadioTabParam(setParam, {
        tag: 'overflow',
        data: [
          {
            option: '',
            value: 'auto'
          },
          {
            option: '',
            value: ''
          }
        ]
      }),
      valueParam: this.getTextareaParam(setParam, {
        tag: 'moduleHeight',
        placeholder: ''
      })
    }
    return Obj
  },
  // 
  getFontStyleSetterParam (setParam) {
    let fontWeightObj = {
      normal: '',
      bold: ''
    }
    let fontWeightData = [
      {
        option: '',
        value: 'normal'
      },
      {
        option: '',
        value: 'bold'
      }
    ]
    for (let i = 1; i <= 9; i++) {
      const num = i * 100
      fontWeightObj[num] = num
      fontWeightData.push({
        option: num,
        value: num
      })
    }
    let Obj = {
      title: '',
      type: 'textStyle',
      sizeParam: this.getTextareaParam(setParam, {
        tag: 'font-size'
      }),
      colorParam: this.getTextareaParam(setParam, {
        tag: 'color'
      }),
      weightParam: this.getSelectorParam(setParam, {
        tag: 'font-weight',
        data: fontWeightData,
        defaultOption: '',
        defaultValue: 'normal',
        optionObj: fontWeightObj
      }),
      textAlignParam: this.getRadioTabParam(setParam, {
        title: '',
        tag: 'text-align',
        defaultValue: 'left',
        data: [
          {
            option: '',
            value: 'left'
          },
          {
            option: '',
            value: 'center'
          },
          {
            option: '',
            value: 'right'
          }
        ]
      }),
      lineHeightParam: this.getTextareaParam(setParam, {
        title: 'px',
        tag: 'line-height',
        placeholder: 'px'
      })
    }
    return Obj
  },
  // 
  getMenuBarStyleSetterParam (setParam) {
    const Obj = {
      type: 'barStyle',
      slParam: this.getSliderParam(setParam, {
        tag: 'width',
        title: ''
      }),
      heightParam: this.getTextareaParam(setParam, {
        tag: 'height'
      }),
      topParam: this.getTextareaParam(setParam, {
        tag: 'top'
      }),
      backgroundParam: this.getTextareaParam(setParam, {
        tag: 'background'
      }),
      borderRadiusParam: this.getTextareaParam(setParam, {
        tag: 'border-radius',
        placeholder: 'px'
      }),
      alignParam: this.getRadioTabParam(setParam, {
        title: '',
        tag: 'align',
        defaultValue: 'left',
        data: [
          {
            option: '',
            value: 'left'
          },
          {
            option: '',
            value: 'center'
          },
          {
            option: '',
            value: 'right'
          }
        ]
      })
    }
    return Obj
  }
}
